.products-container {
	width: 100%;
}
.products-container a {
	text-decoration: none;	
}
.product-container {
	width: 98%;
	display:table; 
	clear:both;
	margin-bottom: 20px;
}
.product-title {
}
.product-thumbnail {
	margin: 20px auto;
	text-align: center;
    padding: 0.8em;
    background-color: #fff;
    border-radius: 0.8em;
}
.product-thumbnail img {
	vertical-align: middle;
}

.amazon-auto-links-credit {
	font-size: 92%;
}

.amazon-product-lowest-new-price-value,
span.offered-price {
    color: #B12704;
    font-weight: bold;
}

.amazon-customer-rating-stars .review-stars svg {
    top: -2px;
}

.products-container .amazon-product-description {
	word-break: break-word;
}